import { Store, CalendarEvent } from '../store';

export const parseDate = (value?: any): Date | void => {
    if (value) {
        const matchWithTime = value.match(/^([0-9]{2})\.([0-9]{2})\.([0-9]{4}) ([0-9]{1,2}):([0-9]{2})$/);
        const matchWithoutTime = value.match(/^([0-9]{2})\.([0-9]{2})\.([0-9]{4})$/);

        if (!matchWithTime && !matchWithoutTime) return;

        const usedMatch = matchWithTime ? matchWithTime : [...matchWithoutTime, 0, 0];

        // extract information
        const year = Number(usedMatch[3]);
        const month = Number(usedMatch[2]) - 1;
        const day = Number(usedMatch[1]);
        const hour = Number(usedMatch[4]);
        const minute = Number(usedMatch[5]);
        // parse it
        const parsed = new Date(Date.UTC(year, month, day, hour, minute));
        const dateIsValid = year === parsed.getFullYear() && month === parsed.getMonth();

        if (!dateIsValid) return;

        return parsed; // return valid date
    }
};

type CalendarEventFromJson = { title: string; start: string; end?: string; desc?: string; color?: string };

const parseEvent = ({ title, start, end, desc, color }: CalendarEventFromJson): CalendarEvent | void => {
    const startDate = parseDate(start);
    const endDate = end ? parseDate(end) : startDate;
    return startDate && endDate ? { title, start: startDate, end: endDate, desc, color } : undefined;
};

export const loadCalendarEvents = () =>
    fetch('/events.json', { headers: { 'cache-control': 'no-cache', pragma: 'no-cache' } })
        .then(r => r.json())
        .then(({ events }) => {
            const parsedEvents = events.map(parseEvent).filter(Boolean);
            Store.set({ events: parsedEvents });
        })
        // eslint-disable-next-line no-console
        .catch(console.error);
