import React from 'react';
import { CalendarEvent, Store } from '../store';
import { IconCalendarDay, IconTimes } from './icons/icon';

const WEEKDAY_NAMES = ['So', 'Mo', 'Di', 'Mi', 'Do', 'Fr', 'Sa'];

const _withLeadingZero = (num: number) => ('0' + num).slice(-2);

const formatTime = (date: Date): string => {
    const hours = date.getUTCHours();
    const minutes = date.getMinutes();
    return hours || minutes ? ` um ${hours}:${_withLeadingZero(minutes)} Uhr` : '';
};
const formatDate = (date: Date) =>
    `${WEEKDAY_NAMES[date.getDay()]}, ${_withLeadingZero(date.getDate())}.${_withLeadingZero(
        date.getMonth() + 1
    )}.${date.getFullYear()}${formatTime(date)}`;

const clearSelectedEvent = () => Store.set({ selectedEvent: null });

export type SelectedEventProps = { selectedEvent: CalendarEvent };

export const SelectedEvent = ({ selectedEvent }: SelectedEventProps) => {
    const { title, start, end, desc } = selectedEvent;

    return (
        <div className="selected-event" onClick={clearSelectedEvent}>
            <div className="close-btn">
                <IconTimes />
            </div>
            <div className="selected-event-container">
                <div className="description">
                    <span className="title">{title}</span>
                    <hr />
                    {+start === +end ? (
                        <>
                            <IconCalendarDay /> {formatDate(start)}
                        </>
                    ) : (
                        <>
                            <div className="date-label">Beginn:</div>
                            {formatDate(start)}
                            <hr />
                            <div className="date-label">Ende:</div>
                            {formatDate(end)}
                        </>
                    )}
                    {desc && <hr />}
                    {desc}
                </div>
            </div>
        </div>
    );
};
