import React from 'react';
import { useSub, NavigationState } from '../store';
import { CalendarPage } from './CalendarPage';
import { DutiesPage } from './DutiesPage';
import { ReviewPage } from './ReviewPage';

export const Content = () => {
    const { navigation } = useSub(({ navigation }) => ({ navigation }));

    if (navigation === NavigationState.DUTIES) return <DutiesPage />;
    if (navigation === NavigationState.REVIEW) return <ReviewPage />;

    return <CalendarPage />;
};
