import React, { ReactNode, useCallback, useEffect, useState } from 'react';
import { Globals } from './globals';
import { IconUndo } from './components/icons/icon';

const reloadPage = () => {
    Globals.window().open(window.location.href, '_self');
};

let newSWInstalledCb = () => {};

const registerServiceWorker = () =>
    Globals.navigator()
        .serviceWorker.register('/sw.js')
        .then((reg: any /* ServiceWorkerRegistration */) => {
            reg.onupdatefound = () => {
                // reg.installing -> the installing worker, or undefined
                // reg.waiting -> the waiting worker, or undefined
                // reg.active -> the active worker, or undefined
                const installingWorker = reg.installing;
                const activeWorker = reg.active;
                installingWorker.onstatechange = () => {
                    // "installing" - the install event has fired, but not yet complete
                    // "installed"  - install complete
                    // "activating" - the activate event has fired, but not yet complete
                    // "activated"  - fully active
                    // "redundant"  - discarded. Either failed install, or it's been
                    //                replaced by a newer version
                    if (installingWorker.state === 'installed' && activeWorker) newSWInstalledCb();
                };
            };
        })
        .catch(() => Globals.window().console.error('service worker registration failed'));

const initServiceWorker = () => {
    if (process.env.NODE_ENV !== 'development') {
        'serviceWorker' in navigator && window.addEventListener('load', registerServiceWorker);
    }
};

const doAfter = (ms: number, cb: () => void): Promise<void> =>
    new Promise(resolve =>
        Globals.window().setTimeout(() => {
            cb();
            resolve();
        }, ms)
    );

export const ServiceWorkerUpdater = () => {
    const [content, setContent] = useState<ReactNode | void>();

    const contentDelayed = useCallback(
        (delay: number, content: ReactNode): Promise<void> => doAfter(delay, () => setContent(content)),
        []
    );

    const initRefresh = useCallback(
        () =>
            Promise.all([
                contentDelayed(0, 'Update gefunden'),
                contentDelayed(1000, 'Update anwenden'),
                contentDelayed(1300, 'Update anwenden.'),
                contentDelayed(1600, 'Update anwenden..'),
                contentDelayed(1900, 'Update anwenden...'),
                contentDelayed(2200, <IconUndo />),
                doAfter(2500, reloadPage),
            ]),
        [] // eslint-disable-line react-hooks/exhaustive-deps
    );

    useEffect(() => {
        newSWInstalledCb = initRefresh;
        initServiceWorker();
    }, []); // eslint-disable-line react-hooks/exhaustive-deps

    if (!content) return null;

    return (
        <div className="sw-update">
            <div className="sw-update-overlay" />
            <div className="sw-update-content">{content}</div>
        </div>
    );
};
