import { createStore } from 'react-use-sub';

export type CalendarEvent = {
    title: string;
    start: Date;
    end: Date;
    desc?: string;
    color?: string;
};

export type Review = {
    name: string;
    crowns: number;
    warnings: number;
    evils: number;
    necessary?: number;
    rewards?: number;
};
export type ReviewsData = {
    reviews: ReadonlyArray<Review>;
    version: string;
    since: string;
};

export enum NavigationState {
    CALENDAR = 'calendar',
    DUTIES = 'duties',
    REVIEW = 'review',
}

type State = {
    navigation: NavigationState;
    events: CalendarEvent[];
    reviewsData: ReviewsData;
    selectedEvent: CalendarEvent | null;
};

const [useSub, Store] = createStore<State>({
    navigation: NavigationState.CALENDAR,
    events: [],
    reviewsData: { version: '', reviews: [], since: '' },
    selectedEvent: null,
});

export { useSub, Store };
