import { Store } from '../store';

export const loadReviews = () =>
    fetch('/reviews.json', { headers: { 'cache-control': 'no-cache', pragma: 'no-cache' } })
        .then(r => r.json())
        .then(({ reviews, version, since }) => {
            const parsedVersion = version || 'konnte nicht geladen werden';
            const parsedSince = since || 'konnte nicht geladen werden';
            Store.set({ reviewsData: { version: parsedVersion, reviews, since: parsedSince } });
        })
        .catch(() => {});
