import React, { useCallback, useMemo, useState, Fragment } from 'react';
import { Util } from '../util/util';
import { IconChevronLeft, IconChevronRight } from './icons/icon';
import { CalendarEvent, Store, useSub } from '../store';

const selectEvent = (selectedEvent: CalendarEvent) => Store.set({ selectedEvent });

const MONTH_NAMES = [
    'Januar',
    'Februar',
    'MÃ¤rz',
    'April',
    'Mai',
    'Juni',
    'Juli',
    'August',
    'September',
    'Oktober',
    'November',
    'Dezember',
];
const WEEKDAY_NAMES = ['Mo', 'Di', 'Mi', 'Do', 'Fr', 'Sa', 'So'];

const _NOW = new Date();
const NOW = new Date(_NOW.getFullYear(), _NOW.getMonth(), _NOW.getDate());
const ONE_DAY = 86400000; // 24 * 60 * 60 * 1000
type MonthAndYear = { month: number; year: number };
const THIS_MONTH_AND_YEAR: MonthAndYear = { month: NOW.getMonth(), year: NOW.getFullYear() };

const calculateDates = (year: number, month: number): Date[] => {
    const firstOfMonth = new Date(year, month, 1);
    const offsetLeft = (firstOfMonth.getDay() + 6) % 7;
    const result = new Array(offsetLeft)
        .fill(undefined)
        .map((_, index) => new Date(year, month, index - offsetLeft + 1));

    for (let i = 1; i <= 31; i++) {
        const next = new Date(year, month, i);
        if (next.getMonth() === month) {
            result.push(next);
        } else {
            break;
        }
    }

    const offsetRight = 7 - result[result.length - 1].getDay();
    return result.concat(
        new Array(offsetRight).fill(undefined).map((_, index) => new Date(year, month + 1, index + 1))
    );
};

type EventClass = 'single' | 'begin' | 'middle' | 'end';

const _eventClass = (isStart: boolean, isEnd: boolean): EventClass => {
    if (isStart) return isEnd ? 'single' : 'begin';
    else return isEnd ? 'end' : 'middle';
};

const _eventMargin = (eventClass: EventClass): number => {
    if (eventClass === 'single') return 1;
    else if (eventClass === 'middle') return 0.5;
    else return 0.75;
};

type DisplayEvent = {
    title?: string;
    backgroundColor?: string;
    zIndex?: number;
    width: string;
    select: () => void;
    className: string;
    row: number;
};
const _getDisplayEvent = (
    event: CalendarEvent,
    date: Date,
    isStart: boolean,
    isEnd: boolean,
    row: number
): DisplayEvent => {
    const titleSpace = Math.min(Math.ceil((+event.end - +date) / ONE_DAY), ((7 - date.getDay()) % 7) + 1);
    const className = _eventClass(isStart, isEnd);
    const width = `calc(${titleSpace * 100}% - ${_eventMargin(className)}em - ${titleSpace > 1 ? 0.5 : 0}em)`;
    const showTitle = isStart || date.getDay() === 1;
    const title = showTitle ? event.title : undefined;
    const zIndex = showTitle ? 1 : undefined;
    return { title, zIndex, select: () => selectEvent(event), width, className, row, backgroundColor: event.color };
};

type DisplayDate = { date: Date; events: DisplayEvent[] };
const calculateEvents = (dates: Date[], events: CalendarEvent[]): DisplayDate[] => {
    const result: DisplayDate[] = dates.map(date => ({ date, events: new Array(3).fill(undefined) }));
    for (const event of events) {
        const { start, end } = event;
        const possibleStartIdx = dates.findIndex(date => +start < +date + ONE_DAY && start > date);
        const possibleEndIdx = dates.findIndex(date => +end < +date + ONE_DAY && end > date);

        const endIdx = possibleEndIdx < 0 && end > dates[dates.length - 1] ? dates.length - 1 : possibleEndIdx;
        const startIdx = possibleStartIdx < 0 && start < dates[0] ? 0 : possibleStartIdx;

        if (startIdx >= 0 && endIdx >= 0) {
            const eventsForDate = result[startIdx].events;
            const row = eventsForDate.findIndex(e => e === undefined);
            if (row >= 0) {
                eventsForDate[row] = _getDisplayEvent(
                    event,
                    dates[startIdx],
                    startIdx === possibleStartIdx,
                    startIdx === possibleEndIdx,
                    row
                );
                for (let i = startIdx + 1; i <= endIdx; i++) {
                    result[i].events[row] = _getDisplayEvent(event, dates[i], false, i === possibleEndIdx, row);
                }
            }
        }
    }
    return result;
};

export const Calendar: React.FC = () => {
    const events = useSub(({ events }) => events);
    const [{ month, year }, setMonthAndYear] = useState(THIS_MONTH_AND_YEAR);
    const dates = useMemo(() => calculateEvents(calculateDates(year, month), events), [year, month, events]);

    const prevMonth = useCallback(
        () =>
            setMonthAndYear(({ month, year }) => ({
                month: month === 0 ? 11 : month - 1,
                year: month === 0 ? year - 1 : year,
            })),
        []
    );
    const nextMonth = useCallback(
        () =>
            setMonthAndYear(({ month, year }) => ({
                month: month === 11 ? 0 : month + 1,
                year: month === 11 ? year + 1 : year,
            })),
        []
    );

    return (
        <div className="calendar">
            <div className="month">
                <span onClick={prevMonth} className="arrow">
                    <IconChevronLeft />
                </span>
                <span className="value">
                    {MONTH_NAMES[month]} {year}
                </span>
                <span onClick={nextMonth} className="arrow">
                    <IconChevronRight />
                </span>
            </div>
            <div className="grid">
                {WEEKDAY_NAMES.map(weekday => (
                    <div key={weekday} className="weekday">
                        {weekday}
                    </div>
                ))}
                {dates.map(({ date, events }) => (
                    <div
                        key={+date}
                        className={Util.classNames(
                            'date',
                            date.getMonth() !== month && 'inactive',
                            +date === +NOW && 'current'
                        )}>
                        <span className="value">{date.getDate()}</span>
                        {events.map((event, index: number) => {
                            if (!event) return <Fragment key={index} />;
                            const { title, zIndex, width, className, row, select, backgroundColor } = event;
                            return (
                                <div
                                    onClick={select}
                                    key={index}
                                    style={{ width, zIndex, backgroundColor }}
                                    className={Util.classNames('event', 'event-' + row, className)}>
                                    {title || <>&nbsp;</>}
                                </div>
                            );
                        })}
                    </div>
                ))}
            </div>
        </div>
    );
};
