import React from 'react';
import { Students } from '../data/students';

const NOW = Date.now();
const COUNT_FIX = 16;

// 1000 * 60 * 60 * 24 = 86400000 (milliseconds of a day)
const countOfAllMondays = Math.floor(((NOW - Date.UTC(2019, 0, 1)) / 86400000 + 1) / 7);

const mondaysToIgnore = [
    Date.UTC(2019, 9, 7),
    Date.UTC(2019, 9, 14),
    Date.UTC(2019, 11, 23),
    Date.UTC(2019, 11, 30),
    Date.UTC(2020, 2, 2),
    Date.UTC(2020, 2, 9),
    Date.UTC(2020, 4, 18),
];

const countOfIgnoredInPast = mondaysToIgnore.filter(timestamp => timestamp <= NOW).length;

const mondaysCount = countOfAllMondays - countOfIgnoredInPast;

const Duties = 'Blumen gieÃŸen,Tafel wischen,Zettel austeilen,MÃ¼ll wegbringen,Licht ausschalten,Fenster schlieÃŸen,Fegen,Reserve'.split(
    ','
);
const DutyImages = 'plant,board,share,trash,light,window,sweep,reserve'.split(',');
type DutyProps = { duty: string; image: string; alt: string; students: string[] };

const modulo = (num: number, base: number) => ((num % base) + base) % base;

const DutiesAndStudents = Duties.map<DutyProps>((duty, index) => {
    const studentIndex = (pos: number) => modulo(-2 * mondaysCount + 2 * index + COUNT_FIX + pos, Students.length);
    const imageName = DutyImages[index];
    return {
        duty,
        image: `/${imageName}.jpg`,
        alt: duty,
        students: [Students[studentIndex(0)], Students[studentIndex(1)]],
    };
});

const Duty = ({ duty, image, alt, students }: DutyProps) => (
    <div className="duty">
        <div className="duty-title">{duty}</div>
        <div className="duty-image">
            <img src={image} alt={alt} />
        </div>
        {students.map((student, index) => (
            <div className="student" key={index}>
                {student}
            </div>
        ))}
        <hr />
    </div>
);

export const DutiesPage = () => (
    <div className="duties">
        {DutiesAndStudents.map((duty, index) => (
            <Duty key={index} {...duty} />
        ))}
    </div>
);
