import React from 'react';
import Icons from './icon-path.json';
import { Util } from '../../util/util';

type IconProps = { className?: string };

const _Icon = (symbol: keyof typeof Icons, props: IconProps = {}, width: number = 512) => (
    <svg className={props.className ? props.className + ' icon' : 'icon'} viewBox={`0 0 ${width} 512`}>
        <path fill="currentColor" d={Icons[symbol]} />
    </svg>
);

export const IconCalendarDay: React.FC<IconProps> = (props: IconProps) => _Icon('CALENDAR_DAY', props, 448);
export const IconChevronRight: React.FC<IconProps> = (props: IconProps) => _Icon('CHEVRON_RIGHT', props, 320);
export const IconChevronLeft: React.FC<IconProps> = (props: IconProps) =>
    _Icon('CHEVRON_RIGHT', { ...props, className: Util.classNames('reflect', props.className) }, 320);
export const IconTimes: React.FC<IconProps> = (props: IconProps) => _Icon('TIMES', props, 352);
export const IconUndo: React.FC<IconProps> = (props: IconProps) => _Icon('UNDO', props);
