import React from 'react';
import { Review } from '../store';

export type ReviewRowProps = { review: Review };

export const ReviewRow = ({ review: { name, crowns, warnings, evils, necessary, rewards } }: ReviewRowProps) => (
    <tr className={necessary ? 'evil' : undefined}>
        <td>{name}</td>
        <td>
            {crowns}
            {rewards ? <span className="rewards">{rewards}</span> : null}
        </td>
        <td>{warnings}</td>
        <td>{evils}</td>
        <td>{necessary || ''}</td>
    </tr>
);
