import React, { useMemo } from 'react';
import { NavigationState, useSub } from '../store';
import { NavigationStore } from '../util/navigation-store';

type NavigationButton = { type: NavigationState; label: string };
const NavigationButtons: ReadonlyArray<NavigationButton> = [
    { type: NavigationState.CALENDAR, label: 'Termine' },
    { type: NavigationState.DUTIES, label: 'Ã„mter' },
    { type: NavigationState.REVIEW, label: 'Kronen' },
];

export const Header = () => {
    const { navigation } = useSub(({ navigation }) => ({ navigation }));

    const buttons = useMemo(
        () =>
            NavigationButtons.filter(button => button.type !== navigation).map(btn => (
                <button onClick={NavigationStore.navigateTo(btn.type)} key={btn.type}>
                    {btn.label}
                </button>
            )),
        [navigation]
    );
    return (
        <header className="App-header">
            <img src="/nb-logo.svg" className="App-logo" alt="logo" />
            <div className="navigation">{buttons}</div>
        </header>
    );
};
